<template>
  <div>
    <p>Home page</p>
    <h3>M1</h3>
    <input v-model="number" type="number" name="m1" min="0" max="360">
    <p>[[randomNumberVal]]</p>
  </div>
</template>

<script>
export default {
  data() {
    return {
      number: 0,
      randomNumberVal : 0
    };
  },


  methods: {
    getNumber() {
      const path = 'http://192.168.0.157:5000/api/random';
      axios.get(path)
        .then((res) => {
          this.randomNumberVal = res.data.randomNumberVal;
        })
        .catch((error) => {
          console.error(error);
        });
    },
  },
  created() {
    this.getNumber();
  },





};
</script>


